import styled from 'styled-components';

export const AdWrapper = styled.div`
  display: flex;
  justify-content: space-between;  /*    */
  gap: 20px;

  position: fixed;      /*   */
  top: 120px;            /*  20px  */
  left: 0;
  right: 0;
  width: 100%;
  max-width: 1200px;    /*     */
  margin: 0 auto;       /*   */

  padding: 0 20px;
  box-sizing: border-box;
  pointer-events: none; /*       */

  z-index: 9999;        /*     */
  
  @media (max-width: 768px) {
    position: static;   /*    */
    width: auto;
    max-width: none;
    margin: 20px auto 0;
    pointer-events: auto;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
`;

export const AdsArea = styled.div`
  pointer-events: auto; /*     */
  width: 320px;
  height: 600px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: 768px) {
    width: 100%;
    height: 120px;
  }
`;

export const LiTitle = styled.li`
    font-size: 20px;
    padding: 5px;
`;
export const Bold = styled.span`
    font-weight: 500;
    font-size: 18px;
    margin-bottom: -10px;
`;
export const ExempleImg = styled.img`
    width: 100%;
`;
export const StaticText = styled.div`
  font-size: 16px;
  padding: 10px 0;
`;