import React, { useState, useRef, useEffect } from 'react';
import html2canvas from 'html2canvas';
import * as S from './AutoNumber.styles';
import * as SS from './Styles';
import AdsBanner from '../components/AdsBanner';
import { Link } from 'react-router-dom';

const allNumbers = Array.from({ length: 45 }, (_, i) => i + 1);
const handleClick = (e) => {
  e.preventDefault(); //   
  const id = e.currentTarget.getAttribute('href').substring(1);
  const el = document.getElementById(id);
  if (el) el.scrollIntoView({ behavior: 'smooth' });
}

const phrases = {
  1: [' !', '   '],
  5: ['5000  ', '1 '],
  10: ['   ', '10  '],
  100: ['   ', '    ']
};

const getBallColor = (number) => {
  if (number >= 1 && number <= 10) return '#f1b800';
  if (number >= 11 && number <= 20) return '#3ba9e0';
  if (number >= 21 && number <= 30) return '#e64545';
  if (number >= 31 && number <= 40) return '#666666';
  if (number >= 41 && number <= 45) return '#7fbf00';
  return '#6200ee';
};

export default function AutoNumber() {
  useEffect(() => {
    document.title = '  - Moneyrise.net';
  }, []);
  const [setCount, setSetCount] = useState(1);
  const [resultSets, setResultSets] = useState([]);
  const [phrase, setPhrase] = useState('');
  const captureRef = useRef();

  const [inputNumbers, setInputNumbers] = useState(Array(6).fill(''));
  const [matchResult, setMatchResult] = useState('');

  const [lastRound, setLastRound] = useState(null);
  const [lastWinningNumbers, setLastWinningNumbers] = useState([]);
  const [lastBonusNumber, setLastBonusNumber] = useState(null);

  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);

  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth <= 768);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // DB    
  const [lottoHistory, setLottoHistory] = useState([]);

  //     
  const [isCapturing, setIsCapturing] = useState(false);

  useEffect(() => {
    setPhrase(getRandomPhrase(1));

    // API DB  
    fetch(`${process.env.PUBLIC_URL}/lotto.json`)
      .then(res => res.json())
      .then(data => {
        setLottoHistory(data);

        if (data.length > 0) {
          const latest = data.reduce((max, cur) => (cur[""] > max[""] ? cur : max), data[0]);
          setLastRound(latest[""]);
          setLastWinningNumbers([
            latest["1"],
            latest["2"],
            latest["3"],
            latest["4"],
            latest["5"],
            latest["6"],
          ]);
          setLastBonusNumber(latest[""]);
        }
      })
      .catch(err => {
        console.error('   :', err);
      });
  }, []);

  const getRandomPhrase = (count) => {
    const arr = phrases[count] || [];
    if (arr.length === 0) return '';
    return arr[Math.floor(Math.random() * arr.length)];
  };

  const pickRandomSix = (arr) => {
    const copy = [...arr];
    const picked = [];
    while (picked.length < 6 && copy.length > 0) {
      const idx = Math.floor(Math.random() * copy.length);
      picked.push(copy.splice(idx, 1)[0]);
    }
    return picked;
  };

  const handleDraw = () => {
    const existingSets = new Set();

    lottoHistory.forEach((round) => {
      const firstPrize = [
        round["1"],
        round["2"],
        round["3"],
        round["4"],
        round["5"],
        round["6"],
      ].sort((a, b) => a - b);
      existingSets.add(firstPrize.join(','));

      const firstPrizeNumbers = firstPrize;
      const bonusNumber = round[""];

      for (let i = 0; i < firstPrizeNumbers.length; i++) {
        const fiveNums = firstPrizeNumbers.filter((_, idx) => idx !== i);
        const twoPrizeCombo = [...fiveNums, bonusNumber].sort((a, b) => a - b);
        existingSets.add(twoPrizeCombo.join(','));
      }
    });

    const newResultSets = [];
    for (let i = 0; i < setCount; i++) {
      let picked;
      let key;
      let tries = 0;

      do {
        picked = pickRandomSix(allNumbers).sort((a, b) => a - b);
        key = picked.join(',');
        tries++;
        if (tries > 1000) {
          alert('    .');
          return;
        }
      } while (existingSets.has(key));

      newResultSets.push(picked);
    }
    setResultSets(newResultSets);
  };

  const handleNumberInput = (index, value) => {
    if (value === '') {
      const updated = [...inputNumbers];
      updated[index] = '';
      setInputNumbers(updated);
      setMatchResult('');
      return;
    }

    if (!/^\d+$/.test(value)) return;

    const num = Number(value);
    if (num < 1 || num > 45) return;

    const updated = [...inputNumbers];
    updated[index] = value;

    if (value.length >= 2) {
      const isDuplicate = updated.some((v, i) => i !== index && Number(v) === num);
      if (isDuplicate) return;
    }

    setInputNumbers(updated);

    if (updated.every(v => /^\d+$/.test(v) && Number(v) >= 1 && Number(v) <= 45)) {
      checkCombination(updated.map(Number));
    } else {
      setMatchResult('');
    }
  };

  const handleInputFocus = (index) => {
    const updated = [...inputNumbers];
    updated[index] = '';
    setInputNumbers(updated);
    setMatchResult('');
  };

  const checkCombination = (input) => {
    let first = 0, second = 0, third = 0, fourth = 0, fifth = 0;

    lottoHistory.forEach(round => {
      const winNums = [
        round["1"], round["2"], round["3"],
        round["4"], round["5"], round["6"]
      ];
      const bonus = round[""];
      const match = input.filter(n => winNums.includes(n)).length;
      const bonusMatch = input.includes(bonus);

      if (match === 6) first++;
      else if (match === 5 && bonusMatch) second++;
      else if (match === 5) third++;
      else if (match === 4) fourth++;
      else if (match === 3) fifth++;
    });

    setMatchResult(`  1 ${first}, 2 ${second}, 3 ${third}, 4 ${fourth}, 5 ${fifth} .`);
  };

  const fallbackCopyTextToClipboard = (text) => {
    const textArea = document.createElement("textarea");
    textArea.value = text;
    textArea.style.position = "fixed";
    textArea.style.top = "-9999px";
    textArea.style.left = "-9999px";
    document.body.appendChild(textArea);
    textArea.focus();
    textArea.select();
    try {
      const successful = document.execCommand('copy');
      if (!successful) alert(' .');
    } catch (err) {
      alert(' .');
    }
    document.body.removeChild(textArea);
  };

  const handleCopy = () => {
    const textToCopy = resultSets.map((set) => set.join(' ')).join('\n');
    try {
      if (navigator.clipboard && typeof navigator.clipboard.writeText === 'function') {
        navigator.clipboard.writeText(textToCopy)
          .then(() => alert('  !'))
          .catch(() => fallbackCopyTextToClipboard(textToCopy));
      } else {
        fallbackCopyTextToClipboard(textToCopy);
      }
    } catch {
      fallbackCopyTextToClipboard(textToCopy);
    }
  };

  const handleCopyInput = () => {
    if (!inputNumbers.every(v => /^\d+$/.test(v) && Number(v) >= 1 && Number(v) <= 45)) {
      alert('6    .');
      return;
    }
    const textToCopy = inputNumbers.join(' ');
    if (navigator.clipboard && typeof navigator.clipboard.writeText === 'function') {
      navigator.clipboard.writeText(textToCopy)
        .then(() => alert('   !'))
        .catch(() => fallbackCopyTextToClipboard(textToCopy));
    } else {
      fallbackCopyTextToClipboard(textToCopy);
    }
  };

  const handleDownloadImage = async () => {
    if (!captureRef.current) return;
    try {
      setIsCapturing(true);
      await new Promise(r => setTimeout(r, 100));
      const canvas = await html2canvas(captureRef.current);
      const imgData = canvas.toDataURL('image/png');
      const link = document.createElement('a');
      link.href = imgData;
      link.download = 'lotto_numbers.png';
      link.click();
    } catch {
      alert('  .');
    } finally {
      setIsCapturing(false);
    }
  };

  const handleDownloadInputImage = async () => {
    if (!captureRef.current) return;
    try {
      setIsCapturing(true);
      await new Promise(r => setTimeout(r, 100));
      const canvas = await html2canvas(captureRef.current);
      const imgData = canvas.toDataURL('image/png');
      const link = document.createElement('a');
      link.href = imgData;
      link.download = 'input_lotto_numbers.png';
      link.click();
    } catch {
      alert('  .');
    } finally {
      setIsCapturing(false);
    }
  };

  const handleChangeCount = (e) => {
    const value = Number(e.target.value);
    setSetCount(value);
    setPhrase(getRandomPhrase(value));
  };

  return (
    <>
    {isMobile && (
      <SS.AdsArea>
        <AdsBanner />
      </SS.AdsArea>
    )}
    <S.Parent>
      <S.MainWord>-       -</S.MainWord>

      {lastRound && lastWinningNumbers.length === 6 && (
        <S.LastWinningContainer>
          <S.LastWinningTitle>   : {lastRound}</S.LastWinningTitle>
          <S.LastWinningNumbers>
            {lastWinningNumbers.map((num) => (
              <S.NumberBall key={num} bgColor={getBallColor(num)}>
                {num}
              </S.NumberBall>
            ))}
            <S.Bonus>+</S.Bonus>
            <S.NumberBall bgColor="#9c27b0">{lastBonusNumber}</S.NumberBall>
          </S.LastWinningNumbers>
        </S.LastWinningContainer>
      )}

      <S.Container ref={captureRef}>
        <S.Title>  </S.Title>
        <S.EmptyBox1/>
        <S.Title>      </S.Title>
        <S.Row>
          <S.RandomPhrase>{phrase}</S.RandomPhrase>
          <S.Chepter1Right>
            <S.StyledSelect value={setCount} onChange={handleChangeCount}>
              {[1, 5, 10, 100].map((num) => (
                <option key={num} value={num}>
                  {num}
                </option>
              ))}
            </S.StyledSelect>
            <S.Button onClick={handleDraw}></S.Button>
          </S.Chepter1Right>
        </S.Row>

        {resultSets.length > 0 && !isCapturing && (
          <S.ButtonDiv>
            <S.StyledButton2 onClick={handleCopy}></S.StyledButton2>
            <S.StyledButton2 onClick={handleDownloadImage}> </S.StyledButton2>
          </S.ButtonDiv>
        )}

        {resultSets.map((set, idx) => (
          <S.NumberList key={idx}>
            {set.map((num) => (
              <S.NumberBall key={num} bgColor={getBallColor(num)}>
                {num}
              </S.NumberBall>
            ))}
          </S.NumberList>
        ))}

        <S.EmptyBox2/>
        <S.Title>      ?</S.Title>
        <S.NumberList>
          {inputNumbers.map((val, idx) => (
            <S.InputBall
              key={idx}
              type="number"
              min="1"
              max="45"
              value={val}
              bgColor={val ? getBallColor(Number(val)) : '#ddd'}
              onChange={(e) => handleNumberInput(idx, e.target.value)}
              onFocus={() => handleInputFocus(idx)}
            />
          ))}
        </S.NumberList>

        {inputNumbers.every(v => /^\d+$/.test(v) && Number(v) >= 1 && Number(v) <= 45) && !isCapturing && (
          <S.ButtonDiv>
            <S.StyledButton2 onClick={handleCopyInput}> </S.StyledButton2>
            <S.StyledButton2 onClick={handleDownloadInputImage}> </S.StyledButton2>
          </S.ButtonDiv>
        )}

        <S.MatchResult>{matchResult}</S.MatchResult>
      </S.Container>

 	  {isMobile && (
      <SS.AdsArea>
        <AdsBanner />
      </SS.AdsArea>
    )}
    

    <S.Container>
      <h1></h1>
        <SS.LiTitle><a href="#percent" onClick={handleClick}> </a></SS.LiTitle> 
        <SS.LiTitle><a href="#info" onClick={handleClick}></a></SS.LiTitle> 
        <SS.LiTitle><a href="#num1" onClick={handleClick}>      </a></SS.LiTitle> 
        <SS.LiTitle><a href="#num2" onClick={handleClick}>      ?</a></SS.LiTitle> 
        <SS.LiTitle><a href="#example" onClick={handleClick}></a></SS.LiTitle> 

        <SS.LiTitle><Link to="/lotto-history">   </Link></SS.LiTitle> 


      <h1 id="percent"> </h1>
      <SS.ExempleImg src={`${process.env.PUBLIC_URL}/images/lotto.png`} />
                .

      <h1 id="info"></h1>
               .
      <br/>     8,145,060   1          . 
           1200     .     2     
         . 
      <br/>      .        .
        

      <h1 id="num1">   <br/>   </h1>
         1  2      .
       1, 5, 10, 100    ,   1 2 3 4 5 6   .
          .
      100      . 
      100 max       10   

      <h1 id="num2">   <br/>   ?</h1>
      6               . 
            .
       
      <h1 id="example"></h1>
      <SS.ExempleImg src={`${process.env.PUBLIC_URL}/images/lotto-exem1.png`} />
           2   ..
      


    </S.Container>
    
    </S.Parent>

	   
    {!isMobile && (
      <SS.AdWrapper>
        <SS.AdsArea>
          <AdsBanner />
        </SS.AdsArea>
        <SS.AdsArea>
          <AdsBanner />
        </SS.AdsArea>
      </SS.AdWrapper>
    )}


    </>
  );
}
